body{
  background-color: #333;
}
.App {
 background-color: #333;
 color: #333;
 font-family: sans-serif;
 font-weight:500;
 display: flex;
 justify-content: center;
 align-items: center;
 height: 97vh;
}
#quote-box {
 border-radius:3px;
 position:relative;
 font-size: 30px;
 text-align: left;
 width:450px;
 padding:40px 40px;
 background-color:#fff;
}
.buttons {
 width:450px;
 margin:auto;
 display:block;  
}

.button {
 height:38px;
 border:none;
 border-radius:5px;
 color: white;
 background-color: #3FE0D0;
 outline:none;
 font-size:0.85em;
 padding: 20px 18px 50px 18px;
 margin-top: 5px;
 opacity:1;
 cursor:pointer;
 float: right;
 }

.button:hover{
 opacity: 0.8;
}
   
#author {
 text-align: center;
}
.fa {
 padding: 20px;
 font-size: 50px;
 width: 50px;
 text-align: center;
 text-decoration: none;
 margin: 5px 10px;
}
.fa-twitter {
 background: #55acee;
 color: white;
 float: left;
   border-radius:5px;
}
.fa-twitter:hover{
 opacity: 0.8;
}
.fa-tumblr{
 background: #35465C;
 color: white;
   border-radius:5px;
}

@media only screen and (max-width: 540px){
 .App {
 width: 100%;
}
 #quote-box {
   position: relative;
   width: 95%;
   padding: 10px 10px;
     font-size: 20px;
   
 }
 .buttons {
 width:95%;
}
 .button {
 padding: 10px 10px 30px 10px;
 }
 
 .fa {
 padding: 10px;
 width: 30px;
 margin: 5px 10px;
}
}

@media only screen and (max-width: 294px){
 #quote-box {
   position: relative;
   width: 95%;
   padding: 10px 10px;
   font-size: 20px;
 }
 .buttons {
   display: flex;
   width:100%;
   justify-content: center;
   
}
 .button {
 font-size:0.5em;
 }
} 
